<template>
  <div>
    <div style="width: 1050px;margin: 75px auto">
      <div style="width:500px;height:500px;display: inline-block;" ref="chart" ></div>
      <div style="width:500px;height:500px;display: inline-block;" ref="chart1"></div>
      <div>
        <div class="newsHeader">工业投资要闻</div>
       <p>·人民网:第三届“强网杯”全国网络安全挑战赛线下赛系列活动在郑启动 <span style="float: right;color: #AEAEAE">2019-06-18 14:30</span></p>
        <el-divider></el-divider>
        <p>·人民网:第三届“强网杯”全国网络安全挑战赛线下赛系列活动在郑启动 <span style="float: right;color: #AEAEAE">2019-06-18 14:30</span></p>
        <el-divider></el-divider>
        <p>·人民网:第三届“强网杯”全国网络安全挑战赛线下赛系列活动在郑启动 <span style="float: right;color: #AEAEAE">2019-06-18 14:30</span></p>
        <el-divider></el-divider>
        <p>·人民网:第三届“强网杯”全国网络安全挑战赛线下赛系列活动在郑启动 <span style="float: right;color: #AEAEAE">2019-06-18 14:30</span></p>
        <el-divider></el-divider>
        <p>·人民网:第三届“强网杯”全国网络安全挑战赛线下赛系列活动在郑启动 <span style="float: right;color: #AEAEAE">2019-06-18 14:30</span></p>
        <el-divider></el-divider>
        <p>·人民网:第三届“强网杯”全国网络安全挑战赛线下赛系列活动在郑启动 <span style="float: right;color: #AEAEAE">2019-06-18 14:30</span></p>
        <el-divider></el-divider>
      </div>
    </div>

  </div>
</template>

<script>
    export default {
        name: "Achievement",
        data(){
            return {
                option:{
                    title : {
                        text: '工业投资行业占比统计',
                        x:'center'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    series : [
                        {
                            name: '访问来源',
                            type: 'pie',
                            radius : '55%',
                            center: ['50%', '60%'],
                            data:'',
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                },
                option1:{
                    title : {
                        text: '工业项目增长统计',
                        x:'center'
                    },
                    xAxis: {
                        type: 'category',
                        data: ''
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: '',
                        type: 'line'
                    }]
                }
            }
        },
        methods:{
            //初始化e-charts
            initCharts () {
                let myChart = this.$echarts.init(this.$refs.chart);
                console.log(this.$refs.chart)
                // 绘制图表
                myChart.setOption(this.option);
            },
            //初始化e-charts1
            initCharts1 () {
                let myChart = this.$echarts.init(this.$refs.chart1);
                console.log(this.$refs.chart1)
                // 绘制图表
                myChart.setOption(this.option1);
            },
            //获取饼图数据
            getPieInfo(){
                var that = this;
                var baseUrl = that.$store.state.url;
                console.log('获取饼图数据')
                this.$axios.get(baseUrl + 'industrialdeclaration/declartionsIndustryProp')
                    .then((res)=>{
                        if (res.data.code==0){
                            console.log('饼图数据')
                            console.log(res)
                            var arr = [];
                            res.data.data.sdata.forEach((item,key)=>{
                                arr.push({value:item.value,name:item.name})
                            })
                            console.log('所需要饼图数据')
                            console.log(arr)
                            that.option.series[0].data = arr;
                            that.initCharts();
                        }
                        console.log(res)
                    })
            },
            //获取折线图数据
            getCharts1(){
                var that = this;
                this.$axios.get('/industrialdeclaration/declartionsRiseTrend')
                    .then((res)=>{
                        if (res.data.code==0){
                            console.log('折线数据');
                            console.log(res)
                            that.option1.series[0].data = res.data.data.sdata;
                            that.option1.xAxis.data = res.data.data.xdata;
                        }
                        that.initCharts1();
                    })
            }

        },
        mounted() {
            this.getPieInfo();
            this.getCharts1();
        },
        created() {
        }
    }
</script>

<style scoped lang="less">
.newsHeader {
  font-size: 18px;
  margin-top: 8px;
  margin-bottom: 31px;
}
</style>



